
import HeaderView from "./components/HeaderView.vue";
import FooterView from "./components/FooterView.vue";
import { mapActions, mapGetters } from "vuex";
import { defineComponent } from "vue";
import { ProdArr } from "@/store/modules/Filter";
import { IArr } from "@/components/product/SingleProduct.vue";
export default defineComponent({
  async created() {
    this.isLoader = true;
    await this.getAllProd();
    await this.getFromStorage();
    await this.getAllBrands();
    await this.getAllCat();
    this.isLoader = false;
  },
  async mounted() {
    window.scrollTo(0, 0);
    const menuButton: HTMLElement | null = document.querySelector(
      ".header__burger"
    ) as HTMLElement;
    const menuButton1: HTMLElement | null = document.querySelector(
      ".header__menu"
    ) as HTMLElement;
    const menuModal: HTMLElement | null = document.querySelector(
      ".header__modal"
    ) as HTMLElement;
    const contentBody: HTMLElement | null = document.querySelector(
      "body"
    ) as HTMLElement;
    const linksList: HTMLElement | null = document.querySelector(
      ".header__list"
    ) as HTMLElement;

    menuButton.onclick = function () {
      menuButton.classList.toggle("active");
      menuButton1.classList.toggle("active");
      menuModal.classList.toggle("active");
      contentBody.classList.toggle("lock");
    };
    linksList.onclick = function (el: Event) {
      let target: HTMLElement = el.target as HTMLElement;
      if (target.classList.contains("mobile__link")) {
        menuButton.classList.toggle("active");
        menuButton1.classList.toggle("active");
        menuModal.classList.toggle("active");
        contentBody.classList.toggle("lock");
      }
    };
    // menuButton1.onclick = function () {
    //   menuButton.classList.remove("active");
    //   menuButton1.classList.remove("active");
    //   menuModal.classList.remove("active");
    //   contentBody.classList.remove("lock");
    // };
  },
  data() {
    return {
      images: [],
      isLoader: false,
      dat: [],
    };
  },
  watch: {
    $route() {
      if (this.getAllProducts.length) {
        this.setTitle();
      }
    },
    async getAllProducts() {
      this.setTitle();
      // if (this.getAllProducts) {
      //   let data = this.getAllProducts;
      //   for (let q = 0; q < data.length; q++) {
      //     let images = await this.getImages(data[q]);
      //     data[q].images = images;
      //   }
      //   this.dat = data;
      // }
    },
  },
  computed: {
    ...mapGetters("Filter", ["getAllProducts"]),
  },
  methods: {
    async getImages(el: any) {
      const comparedImages = [] as string[];
      let arr: IArr[] = [];
      for (let i = 0; i < el.images.length; i++) {
        await fetch(el.images[i])
          .then((response) => response.blob())
          .then((imageBlob) => {
            //console.log(imageBlob.size);
            arr.push({ id: i, length: imageBlob.size as number });
          });
      }
      arr = arr.filter(
        (value, index, self) =>
          index === self.findIndex((t) => t.length === value.length)
      );
      //console.log("arr", arr, arr[0]);

      arr.forEach((ell) => {
        for (let i = 0; i < el.images.length; i++) {
          if (ell.id === i) {
            comparedImages.push(el.images[i]);
          }
        }
      });
      el.images = comparedImages;
      //console.log("comparedImages", comparedImages);
      return comparedImages;
    },
    ...mapActions("Cart", ["getFromStorage"]),
    ...mapActions("Filter", [
      "getAllProd",
      "getFilterParameters",
      "getAllCat",
      "getAllBrands",
    ]),
    setTitle() {
      if (this.getAllProducts) {
        //console.log(this.$route);
        if (this.$route.meta.title) {
          document.title = this.$route.meta.title as string;
        } else {
          if (this.$route.params.id && this.$route.name === "categories") {
            document.title =
              "Category -  " +
              (this.$route.params.id as string) +
              " | K&K Online Store";
          } else if (this.$route.name === "categories") {
            document.title = "Catalog" + " | K&K Online Store";
          } else if (
            this.$route.params.id &&
            this.$route.name === "product-page"
          ) {
            document.title =
              this.getAllProducts.find(
                (el: ProdArr) => el.id === Number(this.$route.params.id)
              ).title +
              " | " +
              this.getAllProducts.find(
                (el: ProdArr) => el.id === Number(this.$route.params.id)
              ).category +
              " | K&K Online Store";
          } else {
            document.title = "K&K Online Store";
          }
        }
      }
    },
  },
  components: {
    HeaderView,
    FooterView,
  },
});
